.bg-white {
  background-color: #fff; }

.bg-lightgrey {
  background-color: #f4f4f4; }

.bg-the-stable {
  background-color: #fee2d1; }

section {
  position: relative; }
  section:nth-child(1) {
    z-index: 1; }
  section:nth-child(2) {
    z-index: 2; }
  section:nth-child(3) {
    z-index: 3; }

.section-padding {
  padding: 2em 1em; }
  @media (min-width: 32em) {
    .section-padding {
      padding: 3em 2em; } }
  @media (min-width: 54em) {
    .section-padding {
      padding: 4em; } }

.grid {
  width: 100%;
  max-width: 81em; }

.hidden {
  display: none; }

.clip {
  position: fixed;
  top: 4em;
  left: 4em;
  z-index: 1; }
  .clip span {
    font-weight: 700;
    font-size: 1.25em;
    color: #ff2222; }

.transition {
  position: fixed;
  top: 0;
  z-index: 9999;
  display: none;
  height: 100%;
  width: 100%;
  background-color: #191919; }
  .transition-out {
    left: 100%;
    display: block; }
  .transition-in {
    left: 0;
    display: block; }
